@import url(https://fonts.googleapis.com/css2?family=Inconsolata&family=Poppins:wght@100;200;300;400;500&display=swap);
body{
    font-family: 'Poppins', sans-serif !important;
}
.card img {
    min-height: 150px;
    width: 100%;
}

ul {
    list-style-type: none;
    padding: 0px;
}

ul li {
    font-size: 0.9rem;
    font-family: 'Inconsolata', monospace;
    color: #7d7d7d;
}

.card-subtitle{
    font-size: 0.8rem;
    color: #575757;
}

.card{
    border: none !important;
    box-shadow: 0px 1px 17px 5px rgba(245,245,245,1);
}
